@use 'variables' as v;

/** Media Queries **/
@mixin telefon {
  @media (min-width: v.$telefon) {
    @content;
  }
}
@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: v.$desktop) {
    @content;
  }
}
@mixin xl_desktop {
  @media (min-width: v.$xl_desktop) {
    @content;
  }
}

@mixin button($bgColor, $colorText) {
  background-color: $bgColor;
  padding: 1.5rem 4rem;
  color: $colorText;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;

  &:hover {
    background-color: darken($bgColor, 10%);
    cursor: pointer;
  }
}

@mixin grid($columnes, $espai) {
  display: grid;
  gap: $espai;
  grid-template-columns: repeat($columnes, 1fr);
}

@mixin contenidor {
  width: min(95%, 140rem);
  margin: 0 auto;
}

@mixin contenidor-md {
  width: min(95%, 80rem);
  margin: 0 auto;
}

@mixin contenidor-sm {
  width: min(95%, 60rem);
  margin: 0 auto;
}

@mixin headings {
  font-family: v.$font-principal;
  font-weight: v.$black;
  margin: 2rem 0;
}

@mixin numero-pagina {
  @include headings;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

@mixin descripcio-pagina {
  text-align: center;
  font-size: 2rem;
  font-weight: v.$thin;
  color: v.$gris-fosc;
  margin-bottom: 5rem;
}

@mixin text-fluid($min, $max) {
  font-size: clamp($min, -0.8rem + 10vw, $max);
}

@mixin text-gradient($inici, $final) {
  background: linear-gradient(to right, $inici, $final);
  background-clip: text;
  color: transparent;
}

@mixin logo {
  color: v.$blanc;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  @include desktop {
    text-align: left;
    margin: 0;
  }
}

@mixin menu-social($color, $size) {
  &__link {
    &::before {
      color: $color;
      font-size: $size;
      display: inline-block;
      font-style: normal;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      font-family: 'Font Awesome 6 Brands';
    }

    &[href*='facebook.com']::before {
      content: '\f39e';
    }
    &[href*='github.com']::before {
      content: '\f09b';
    }

    &[href*='tiktok.com']::before {
      content: '\e07b';
    }
    &[href*='instagram.com']::before {
      content: '\f16d';
    }
    &[href*='youtube.com']::before {
      content: '\f167';
    }
    &[href*='twitter.com']::before {
      content: '\f099';
    }
  }

  &__hidden {
    display: none;
  }
}

@mixin link {
  all: unset;
  color: v.$blanc;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: v.$bold;

  &:hover {
    color: v.$rosa;
    cursor: pointer;
  }
}

@mixin shadow {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

@mixin reset-llista {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin tags {
  @include reset-llista;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;

  > li {
      background-color: v.$blau;
      padding: 1rem;
      border-radius: .5rem;
      font-size: 1.4rem;
      font-weight: v.$bold;
      color: v.$blanc;
      cursor: pointer;

      &:hover {
        background-color: v.$vermell;
      }
  }


}

@mixin card {
  border: 1px solid lighten(v.$gris, 10);
  padding: 3rem;

  &__nom {
      font-weight: v.$black;
      font-size: 2.6rem;
      margin: 2rem 0;
  }
}

@mixin bg-gradient($bg_color) {
  background-image: linear-gradient(to left top, $bg_color, lighten($bg_color, 15%));
}
