@use 'base/variables' as v;
@use 'base/mixins' as m;

.hores {
    @include m.reset-llista;
    @include m.grid(1, 1rem);
    @include m.tablet {
        @include m.grid(2, 1rem);
    }

    &__hora {
        color: v.$blau;
        border: 1px solid v.$blau;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;

        &:hover {
            cursor: pointer;
        }

        &--seleccionada {
            @extend .hores__hora;
            background-color: v.$blau;
            color: v.$blanc;
        }

        &--deshabilitada {
            @extend .hores__hora;
            border: 1px solid v.$gris;
            color: v.$gris;
            opacity: .5;

            &:hover {
                cursor: not-allowed;
            }
        }
    }
}